import * as React from "react";
import { graphql } from "gatsby";

import Layout from "../components/Layout";

import logo from "../images/logo.png";
import imp1 from "../images/index-1.png";
import imp2 from "../images/index-2.png";
import imp3 from "../images/index-3.png";
import cinema from "../images/cinema.png";

// markup
const IndexPage = ({ data }) => {
  return (
    <Layout>
      <div className="grid-container halves">
        <a href="/" className="u-align-left m-align-center">
          <img src={logo} alt="Pick 'Imp' Mix" />
        </a>
        <div className="home-imps">
          <img src={imp1} alt="Red Imp" />
          <img src={imp2} alt="Green Imp" />
          <img src={imp3} alt="Blue Imp" />
        </div>
      </div>

      <div className="grid-container full u-align-left m-align-center">
        <p>
          Pick 'imp' Mix is a project that my dad and i completed over the
          summer for me to practice drawing retro style pixel characters based
          on my enjoyment of playing 8-bit and 16-bit games and for my dad to
          learn more about blockchains. we started with a set of 512 unique imps
          all with differing rarities based on their randomly selected
          attributes.
        </p>
      </div>
      <div className="grid-container thirds u-align-right m-align-center">
        <img className="imp-image" src={cinema} alt="Cinematic Icons" />
        <div className="span-2">
          <h4>Cinematic Icons</h4>
          <p>
            As part of the launch of the set we created 8 one-off imps that
            represented some of our favourite movie characters.
          </p>
        </div>
      </div>

      <div
        className="grid-container full u-align-left"
        style={{ marginTop: "2rem" }}
      >
        <h4>{data.allMetaJson.totalCount} Imps minted</h4>

        <table class="u-full-width">
          <thead>
            <tr>
              <th>Imp #</th>
              <th>IPFS CID Hash</th>
              <th>&nbsp;</th>
            </tr>
          </thead>
          <tbody>
            {data.allMetaJson.nodes
              .sort((a, b) => a.id - b.id)
              .map((element) => (
                <tr>
                  <td>{element.id}</td>
                  <td>
                    <a
                      href={element.image.replace(
                        "ipfs:/",
                        "https://gateway.pinata.cloud/ipfs"
                      )}
                      target="_blank"
                      rel="noreferrer"
                    >
                      {element.image.split("//")[1]}
                    </a>
                  </td>
                  <td>
                    <a href={`/imp/${element.id}`}>More Info</a>
                  </td>
                </tr>
              ))}
          </tbody>
        </table>
      </div>
    </Layout>
  );
};

export const query = graphql`
  query {
    allMetaJson {
      nodes {
        id
        attributes {
          trait_type
          value
        }
        background_color
        description
        external_url
        image
        name
      }
      totalCount
    }
  }
`;

export default IndexPage;
